/* Counter Style One */
.counter-style-one{
    position: relative;
    z-index: 1;
}
.counter-style-one:after{
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;
    background-color: var(--red-dark);
    mix-blend-mode: multiply;
    z-index: -1;
}
.counter-style-one .counter-item .icon{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #fff;
    margin-bottom: 21px;
}
.counter-style-one .counter-item .icon i{
    font-size: 50px;
    line-height: 1;
    color: var(--red-dark);
}
.counter-style-one .counter-item h2.number{
    color: #fff;
}
.counter-style-one .counter-item p{
    font-weight: 500;
    color: #fff;
}


/* Counter Style Two */
.counter-style-two .counter-wrapper{
    position: relative;
    border: 1px solid rgba(27, 27, 32, 0.08);
    background-color: #fff;
    padding-top: 0;
}
.counter-style-two .counter-wrapper .shape-icon{
    position: absolute;
    z-index: -1;
}
.counter-style-two .counter-wrapper .shape-icon.shape-icon-one{
    top: -70px;
    left: -70px;
}
.counter-style-two .counter-wrapper .shape-icon.shape-icon-two{
    bottom: -50px;
    right: -50px;
}
.counter-style-two .counter-item{
    padding: 60px 30px;
}
.counter-style-two .counter-item .icon{
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: rgba(0, 102, 255, 0.07);
    margin-bottom: 20px;
}
.counter-style-two .counter-item .icon i{
    font-size: 50px;
    line-height: 0;
    color: var(--blue-dark);
}
.counter-style-two .counter-item p{
    font-weight: 500;
    line-height: 20px;
}
.counter-style-two .counter-column{
